/* FAQ 카드 컨테이너 (그리드) */
.FAQ-buttons {
  display: grid;
  grid-template-columns: 1fr 1fr; /* 2열 */
  gap: 16px;                       /* 카드 사이 간격 */
  
  position: absolute;
  top: 635px;                      /* 적절한 위치 */
  left: 50%;
  transform: translateX(-50%);
  width: 390px;                    /* 전체 컨테이너 너비 */
  
  padding: 0;
  margin: 0;
}


/* 개별 FAQ 카드 래퍼 - 꼭 필요 */
.FAQ-card {
  position: relative;              /* 편집 버튼 절대 위치 기준점 */
  width: 187px;
  height: 79px;
}


/* 메인 FAQ 버튼 */
.FAQ-button {
  /* 크기 - 카드 전체 크기 */
  width: 100%;
  height: 100%;
  padding: 16px;
  
  /* 레이아웃 */
  display: flex;
  justify-content: center;
  align-items: center;
  
  /* 모양 */
  border-radius: 16px;
  border: 0.5px solid rgba(255, 255, 255, 0.3);
  
  /* 배경 */
  background: rgba(255, 255, 255, 0.08);
  backdrop-filter: blur(8px);
  
  /* 그림자 */
  box-shadow: 0px 4px 12px 0px rgba(0, 0, 0, 0.15);
  
  /* 텍스트 */
  font-family: 'Poppins', sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: #FFFFFF;
  text-align: left;
  line-height: 22px;
  
  /* 인터랙션 */
  cursor: pointer;
  transition: all 0.2s ease;
  
  /* 기본 스타일 초기화 */
  margin: 0;
  outline: none;
}

/* FAQ 버튼 호버 효과 */
.FAQ-button:hover {
  background: rgba(255, 255, 255, 0.12);
  transform: translateY(-2px);
  box-shadow: 0px 6px 16px 0px rgba(0, 0, 0, 0.25);
}

/* FAQ 버튼 클릭 효과 */
.FAQ-button:active {
  transform: translateY(0);
  box-shadow: 0px 2px 8px 0px rgba(0, 0, 0, 0.2);
}

/* 편집 버튼 */
.FAQ-edit-btn {
  /* 위치 - 카드 우측 상단 */
  position: absolute !important;   /* 중요: 다른 스타일 덮어쓰기 방지 */
  top: -7px;
  right: 0px;
  
  /* 크기 */
  width: 20px;
  height: 20px;
  
  /* 모양 */
